  text-align: center;
}

/** Media Queries **/
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #1a1b15;
  font-family: "Poppins", sans-serif;
}

h1 {
  color: #0da6f3;
  text-align: center;
  font-family: "Poppins", sans-serif;
}

h2 {
  color: #0da6f3;
  text-align: center;
  font-family: "Poppins", sans-serif;
}

.contenedor-estetica {
  height: 100vh;
}
@media (min-width: 768px) {
  .contenedor-estetica {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-estetica .imagen {
  height: 50%;
  width: 100%;
  background-image: url(../../../build/img/1.webp);
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .contenedor-estetica .imagen {
    height: 100%;
  }
}
.contenedor-estetica .app {
  padding-top: 1rem;
  margin: 1rem 1rem;
}
.contenedor-estetica .app .seccion {
  display: none;
}
.contenedor-estetica .app .seccion .text-center {
  color: #FFFFFF;
}

.mostrar-seccion {
  display: block !important;
}

.tabs {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
}
.tabs button {
  cursor: pointer;
  height: 3rem;
  background-color: #0da6f3;
  border: none;
  font-size: 1rem;
}
.tabs button.actual {
  background-color: coral;
}
.tabs button:hover {
  background-color: coral;
}

@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 0.1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.servicio {
  font-weight: 700;
  cursor: pointer;
  text-align: center;
  background-color: #e1e1e1;
  border: 2px solid #0da6f3;
  border-radius: 15px;
}
.servicio .precio-servicio {
  color: #0da6f3;
}
.servicio:hover {
  background-color: coral;
}

.seleccionado {
  background-color: coral;
}

.paginacion {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.paginacion button {
  background-color: #0da6f3;
  padding: 0.5rem 1rem;
  margin-top: 2rem;
  display: block;
  text-align: center;
  color: #FFFFFF;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
.paginacion button.ocultar {
  visibility: hidden;
}
.paginacion button:hover {
  background-color: coral;
}

.formulario {
  width: 100%;
}
.formulario label {
  color: #FFFFFF;
}
.formulario input {
  border: none;
  border-radius: 5px;
  height: 1.5rem;
}

.campo {
  margin: 2rem;
  display: flex;
}
.campo label {
  flex: 1;
}
.campo input {
  flex: 5;
}

.fecha-hora {
  margin: 2rem;
  display: flex;
}
.fecha-hora label {
  flex: 1;
}
.fecha-hora input[type=date] {
  width: 2rem;
  flex: 3;
}
@media (min-width: 768px) {
  .fecha-hora input[type=date] {
    flex: 3;
  }
}
.fecha-hora label[for=hora] {
  text-align: center;
  flex: 1;
}
.fecha-hora input[type=time] {
  flex: 0;
}

.alerta {
  width: 80%;
  height: 2rem;
  margin: 0 auto;
  padding-top: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  display: block;
}
.alerta.error {
  color: red;
  border: red 1px solid;
  border-radius: 5px;
}

.total {
  text-align: right;
}

.invalidar-cita {
  text-align: center;
  color: #FFFFFF;
}

.contenido-resumen h3 {
  color: #0da6f3;
  text-align: center;
}
.contenido-resumen p {
  color: #FFFFFF;
}
.contenido-resumen p span {
  color: #0da6f3;
}

.resumen-servicios {
  padding-top: 1rem;
}
.resumen-servicios .contenedor-servicio {
  margin-bottom: 2rem;
  border-bottom: 2px #FFFFFF solid;
}
.resumen-servicios .contenedor-servicio .precio {
  color: #0da6f3;
}